.cart-modal {
  background-color: #fff;
  color: black;
  padding: 20px;
  max-width: 300px;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  margin-top: 20px;
  margin-bottom: 20px;
}

.cart-modal ul {
  list-style-type: none;
  padding: 0;
  margin-bottom: 20px;
}

.cart-modal ul li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.cart-modal ul li button {
  background-color: #ff4d4d;
  border: none;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
}

.cart-modal ul li button:hover {
  background-color: #d43636;
}

.cart-modal .buy-button {
  background-color: #4caf50;
  color: white;
  border: none;
  padding: 10px;
  width: 100%;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  font-size: 16px;
}

.cart-modal .buy-button:hover {
  background-color: #45a049;
}

.cart-modal h2,
.cart-modal h3 {
  text-align: center;
}

/* Flexbox для центрирования корзины с карточками */
.product-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.category-container,
.subcategory-container,
.product-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.back-button {
  background-color: red;
  color: white;
  border: none;
  padding: 10px;
  width: 100%;
  max-width: 400px;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 20px;
  font-size: 16px;
}

.back-button:hover {
  background-color: #e60000;
}

.cart-modal-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

.cart-modal ul li button {
  background-color: #ff4d4d;
  border: none;
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  margin-left: 10px;
}

.cart-modal ul li button:hover {
  background-color: #d43636;
}

.empty-cart-message {
  display: flex;
  justify-content: center; /* Центрирование по горизонтали */
  align-items: center; /* Центрирование по вертикали */
  height: 100%; /* Убедитесь, что контейнер занимает достаточную высоту */
  font-size: 18px; /* Размер шрифта можно изменить по желанию */
  color: #333; /* Цвет текста */
  text-align: center;
}
