.app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: black;
  color: white;
}

.card {
  background-color: white;
  color: black;
  margin: 10px;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.card-image {
  max-width: 100px;
  margin-bottom: 10px;
}

.quantity-controls {
  display: flex;
  justify-content: center;
  margin-top: 10px;
}

.quantity-controls button {
  margin: 0 5px;
}
